h1 {
    font-size: 3em;
    text-align: center;
}

span {
    font-size: 1.4em;
}

.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    position: relative;
}
.image-list img {
    max-width: 100%;
    padding: 10px;
    position: relative;
}

.ui.segment {
    position: relative;
    height: 40vh;
    border: none;
    box-shadow: none;
}


.ui.segment::before {
    content: "";
    position: absolute;
    left:0;
    top:0;
    width: 100%;
    height: 100%;
    background-image: url("https://images.unsplash.com/photo-1582555172866-f73bb12a2ab3?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2000&q=80");
    opacity:0.4;
    clip-path: polygon(0 0, 100% 0, 100% 79%, 0% 100%);
    background-position: center;

}